.header{
    margin: 0 auto;
    width: 100%;
    border-bottom:1px solid rgba(84,84,84,0.7);
}

@media screen and (max-width: 879px) {
    .header{
        width: calc(100% - 19px*2);
        margin: 0 auto;
    }
}

@media screen and (max-width: 639px) {
    .header{
        width: 100%;
    }
}